.card {
  background:cover;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 250px;
  display: inline-block;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  

}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
 display: inline;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
  width: 100%;
  font-size: 2rem;
  color: #2A77AE;
  margin-bottom: 10px;
}

.title {
  float: left;
}

.scores {
  float: right;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

